import request from '@/common/utils/request';
// 查询组织的损益结算
export function queryOrgRevenueProfile(data) {
  return request({
    url: '/fintech/v1/settlement/revenue/org/queryOrgRevenueProfile',
    method: 'post',
    data,
  });
}

// 查询所有模版名称
